.ButtonsGrid {
	--buttons-grid-space: 20px;
	display: flex;
	flex-wrap: wrap;
	padding: var(--buttons-grid-space);
	gap: var(--buttons-grid-space);

	.Column	 {
		display: flex;
		flex-basis: 1 1 140px;
		flex-direction: column;
		gap: var(--buttons-grid-space);

		.Block	 {
			display: flex;
			flex-direction: column;
			align-items: start;
			gap: var(--buttons-grid-space);

			.formBlock	 {
				display: flex;
				flex-direction: column;
				align-items: start;
				gap: var(--buttons-grid-space);
			}
		}
	}
}